import React,{useState} from 'react'


export default function TextForm(props) {
    const  handleClick = ()=>{
        // console.log('upper case clicked '+ text);
        setText(text.toUpperCase());
        props.showAlert('converted to upper case','no');
          }
          const  handleLoClick = ()=>{
            // console.log('upper case clicked '+ text);
            setText(text.toLocaleLowerCase());
              }
              const  handleChangeClick = ()=>{
               //change s to $
               let temp ='';
               for(var i=0;i<text.length;i++){
                
               if( text[i] === 's' || text[i] === 'S'){
                temp += '$';
               
               }else{
                temp += text[i];
               }
               
               }
               setText(temp)
                  }
  const  handleOnChange = (event)=>{
setText(event.target.value);
  }
    const [text, setText] = useState('');
 // setText('hello');
  return (
  <>
  <div className="container">
    <label htmlFor="myBox" className="form-label">{props.heading}</label>
    <textarea className="form-control" value={text} onChange={handleOnChange} id="textBox" rows="8" />
    <button type="submit" onClick={handleClick} className="btn btn-primary my-2 mx-2">Convert to upperCase</button>
    <button type="submit" onClick={handleLoClick} className="btn btn-primary my-2 mx-2">Convert to lowerCase</button>
    <button type="submit" onClick={handleChangeClick} className="btn btn-primary my-2 mx-2">Convert s to $</button>
    </div>
<div className="container my-2">
    <h1>Your text summary</h1>
    <p>{text.split(" ").length-1} words, {text.length} chars </p>
    <p>{text.split(" ").length * 0.08} Minutes read</p>
    <h2>Preview</h2>
    <p>{text}</p>
    
    </div>
  </>
  )
}
